import React, { Component, Fragment } from 'react'
import {
  Backdrop,
  Fade,
  Modal,
  Container,
  Icon,
  FormControl,
  InputLabel,
  Input,
  FormHelperText,
  TextField,
  MenuItem,
  Button,
  Typography,
} from '@material-ui/core'
import ChevronRight from '@material-ui/icons/ChevronRight'
import _isEqual from 'lodash/isEqual'
import _trim from 'lodash/trim'
import _split from 'lodash/split'
import { withStyles } from '@material-ui/core/styles'
import PropTypes from 'prop-types'
import GradientStepper from '@Components/Stepper/GradientStepper'
import styles from './styles'

const STEPS = [
  {
    label: 'ข้อมูล นศ.',
    icon: (
      <Icon
        className="fas fa-user-graduate"
        style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}
      />
    ),
  },
  {
    label: 'ข้อมูลส่วนตัว',
    icon: (
      <Icon
        className="fas fa-user"
        style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}
      />
    ),
  },
  {
    label: 'ข้อมูลการติดต่อ',
    icon: (
      <Icon
        className="fas fa-phone-volume"
        style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}
      />
    ),
  },
]

const MAJOR = [
  { label: 'วิศวะกรรมซอฟต์แวร์', value: 'SE' },
  { label: 'การจัดการสมัยใหม่และเทคโนโลยีสารสนเทศ', value: 'MMIT' },
  { label: 'แอนิเมชั่นและวิชวลเอฟเฟกต์', value: 'ANI' },
  { label: 'ดิจิทัลเกม', value: 'DG' },
  { label: 'บูรณาการอุตสาหกรรมดิจิทัล', value: 'DII' },
]

const defaultForm = {
  stdID: '',
  major: '',
  name: '',
  nickName: '',
  gender: '',
}

export class RegisterContainer extends Component {
  static propTypes = {
    classes: PropTypes.object.isRequired,
    visible: PropTypes.bool,
    handleClose: PropTypes.func,
  }

  static defaultProps = {
    visible: false,
    handleClose: () => {},
  }

  state = {
    currentStep: 1,
    formValues: { ...defaultForm },
    errors: { ...defaultForm },
  }

  handleChange = name => event => {
    const { setValue } = this
    const { value } = event.target
    event.persist()
    if (name === 'stdID') {
      const regex = /[^0-9]/g
      const filtered = value.replace(regex, '')
      setValue(name, filtered.substring(0, 9))
    } else {
      setValue(name, value)
    }
  }

  setValue = (name, value) =>
    this.setState(state => ({
      formValues: {
        ...state.formValues,
        [name]: value,
      },
      errors: {
        ...state.errors,
        [name]: '',
      },
    }))

  handleSubmitStep1 = () => {
    const stdIDValidate = /[0-9]{9}/g
    const { formValues } = this.state
    const newErrors = { ...defaultForm }
    if (!formValues.stdID) {
      newErrors.stdID = 'กรุณากรอกรหัสนักศึกษา'
    } else if (!formValues.stdID.match(stdIDValidate)) {
      newErrors.stdID = 'รหัสนักศึกษาต้องมี9ตัวอักษร'
    }

    if (!formValues.major) {
      newErrors.major = 'กรุณาเลือกสาขา'
    }
    if (_isEqual(newErrors, defaultForm)) {
      this.setState({ currentStep: 1 })
    } else {
      this.setState({ errors: newErrors })
    }
  }

  handleSubmitStep2 = () => {
    const { formValues } = this.state
    const newErrors = { ...defaultForm }
    if (!formValues.name) {
      newErrors.name = 'กรุณากรอกชื่อและนามสกุล'
    }
    if (_isEqual(newErrors, defaultForm)) {
      this.setState({ currentStep: 2 })
    } else {
      this.setState({ errors: newErrors })
    }
  }

  handleSubmitStep3 = () => {
    this.props.handleClose()
  }

  handleSubmit = event => {
    event.preventDefault()
  }

  ButtonStep = ({ onClick = () => {}, title = '', showIcon = false }) => {
    const { classes } = this.props
    return (
      <div className={classes.stepButtonContainer}>
        <Button
          variant="contained"
          color="secondary"
          onClick={onClick}
          className={classes.stepButton}
        >
          <Typography align="center" className={classes.stepButtonText}>
            {title}
          </Typography>
          {showIcon && <ChevronRight />}
        </Button>
      </div>
    )
  }

  renderFormStep = () => {
    const { classes } = this.props
    const { formValues, errors, currentStep } = this.state
    const { ButtonStep } = this
    if (currentStep === 0) {
      return (
        <Fragment>
          <div className={classes.inputContainer}>
            <FormControl className={classes.inputStyle} error={!!errors.stdID}>
              <InputLabel htmlFor="stdID">รหัส นักศึกษา</InputLabel>
              <Input
                id="stdID"
                value={formValues.stdID}
                onChange={this.handleChange('stdID')}
                aria-describedby="stdID-text"
              />
              <FormHelperText id="stdID-text">{errors.stdID || ' '}</FormHelperText>
            </FormControl>
            <TextField
              id="major"
              select
              label="สาขา"
              className={classes.inputStyle}
              value={formValues.major}
              onChange={this.handleChange('major')}
              error={!!errors.major}
              helperText={errors.major || ' '}
            >
              {MAJOR.map(option => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </TextField>
          </div>
          <ButtonStep title="ขั้นตอนต่อไป" onClick={this.handleSubmitStep1} showIcon />
        </Fragment>
      )
    }
    if (currentStep === 1) {
      return (
        <Fragment>
          <div className={classes.inputContainer}>
            <FormControl className={classes.inputStyle} error={!!errors.name}>
              <InputLabel htmlFor="name">ชื่อ-สกุล</InputLabel>
              <Input
                id="name"
                value={formValues.name}
                onChange={this.handleChange('name')}
                aria-describedby="name-text"
              />
              <FormHelperText id="name-text">{errors.name || ' '}</FormHelperText>
            </FormControl>
          </div>
          <ButtonStep title="ขั้นตอนสุดท้าย" onClick={this.handleSubmitStep2} showIcon />
        </Fragment>
      )
    }
    if (currentStep === 2) {
      return (
        <Fragment>
          <div className={classes.inputContainer}></div>
          <ButtonStep title="เสร็จสิ้น" onClick={this.handleSubmitStep3} />
        </Fragment>
      )
    }
    return <></>
  }

  render() {
    const { visible, classes, handleClose } = this.props
    const { currentStep } = this.state

    return (
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={visible}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={visible}>
          <Container className={classes.paper}>
            <GradientStepper activeStep={currentStep} steps={STEPS} />
            <form className={classes.formStyle}>{this.renderFormStep()}</form>
          </Container>
        </Fade>
      </Modal>
    )
  }
}

export default withStyles(styles)(RegisterContainer)
